/* montserrat-regular - latin */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: local(''),
    url('../../fonts/montserrat-v24-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */
      url('../../fonts/montserrat-v24-latin-regular.woff') format('woff'),
    /* Modern Browsers */ url('../../fonts/montserrat-v24-latin-regular.ttf')
      format('truetype'); /* Safari, Android, iOS */
}

/* roboto-regular - latin_cyrillic */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local(''),
    /* IE6-IE8 */ url('../../fonts/roboto-v30-latin_cyrillic-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('../../fonts/roboto-v30-latin_cyrillic-regular.woff') format('woff'),
    /* Modern Browsers */
      url('../../fonts/roboto-v30-latin_cyrillic-regular.ttf')
      format('truetype');
}
